.Nav {
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.0975);
  top: 0;
  width: 100%;
  z-index: 2;
  -webkit-transition: height 0.2s ease-in-out;
  transition: height 0.2s ease-in-out;
  height: 77px;

}
.Nav-menus {
  display: flex;
  flex-direction: row;
  height: 77px;
  width: 70%;
  margin: 0 auto;
  padding: 26px 40px;
  flex: 0 1 auto;
  text-align: center;
}
.Nav-brand-logo {
  display: block;
  background-position: -176px 0px;
  background-image: url(../../sprite.png);
  background-size: 405px 379px;
  background-repeat: no-repeat;
  height: 35px;
  width: 176px;
  text-indent: -1000%
}
input{
  display: inline-block;
  margin-left: 15rem;
  font-size: large;
}
.Nav-link {
 margin-left: 20rem;
}
